import React, {
  useRef,
  forwardRef,
  useEffect,
  useState,
  useCallback,
  useContext,
} from "react";
import {
  useTable,
  usePagination,
  useRowSelect,
  useGlobalFilter,
} from "react-table";
import { useNavigate, useLocation } from "react-router-dom";
import styled from "styled-components";
import Search from "./Search";
import axios from "axios";
import moment from "moment";
import "moment/locale/ko";
import querystring from "querystring";
import * as queryString from "querystring";
import { AuthContext } from "../../context/AuthContext";

const IndeterminateCheckbox = forwardRef(({ indeterminate, ...rest }, ref) => {
  const defaultRef = useRef();
  const resolvedRef = ref || defaultRef;

  useEffect(() => {
    resolvedRef.current.indeterminate = indeterminate;
  }, [resolvedRef, indeterminate]);

  return (
    <InputRadio
      style={{ color: "#203a8f" }}
      type="checkbox"
      ref={resolvedRef}
      {...rest}
    />
  );
});

const TableAdmin = ({
  columns,
  _,
  setSelectedMentoList,
  setAllMentoList,
  startDate,
  endDate,
  searchTerm,
  setSearchTerm,
}) => {
  const [me] = useContext(AuthContext);
  const navigation = useNavigate();
  const location = useLocation();
  const query = queryString.parse(location.search.replace("?", ""));

  const [data, setData] = useState([]);
  const [currentPage, setCurrentPage] = useState(parseInt(query.page) || 1);
  const [, setCountAll] = useState(0);
  const [totalPage, setTotalPage] = useState(0);
  const [startPage, setStartPage] = useState(0);
  const [endPage, setEndPage] = useState(0);
  //    
  const [maxPage, setMaxPage] = useState(parseInt(query.maxPage) || 5);
  //    
  const [perPage, setPerPage] = useState(parseInt(query.perPage) || 9);

  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    prepareRow,
    rows,
    setGlobalFilter,
    state: { selectedRowIds },
  } = useTable(
    {
      columns,
      data,
      manualPagination: true,
    },
    usePagination,
    useRowSelect,
    (hooks) => {
      hooks.visibleColumns.push((columns) => [
        // Let's make a column for selection
        {
          id: "selection",
          // The header can use the table's getToggleAllRowsSelectedProps method
          // to render a checkbox
          Header: ({ getToggleAllPageRowsSelectedProps }) => (
            <div>
              <IndeterminateCheckbox {...getToggleAllPageRowsSelectedProps()} />
            </div>
          ),
          // The cell can use the individual row's getToggleRowSelectedProps method
          // to the render a checkbox
          Cell: ({ row }) => (
            <div>
              <IndeterminateCheckbox {...row.getToggleRowSelectedProps()} />
            </div>
          ),
        },
        ...columns,
      ]);
    }
  );

  const fetchData = useCallback(
    ({ page }) => {
      fetchAPIData({
        page,
        startDate,
        endDate,
      }).catch((err) => {
        console.error(err);
      });
    },
    [searchTerm, startDate, endDate]
  );

  const fetchAPIData = async ({ page, startDate, endDate }) => {
    let queryParams = { page, perPage, maxPage, searchTerm };
    if (startDate && endDate) {
      queryParams.startDate = moment(startDate).format("YYYY-MM-DD");
      queryParams.endDate = moment(endDate).format("YYYY-MM-DD");
    }

    try {
      const { data } = await axios.post(
        `/api/admin/list?${querystring.stringify(queryParams)}`
      );
      navigation({
        pathname: location.pathname,
        search: `?${querystring.stringify(queryParams)}`,
      });
      const paging = data.paging;
      setCountAll(paging.countAll);
      setStartPage(paging.startPage);
      setEndPage(paging.endPage);
      setMaxPage(paging.maxPage);
      setTotalPage(paging.totalPage);
      setData(data.data);
    } catch (e) {
      console.log("Error while fetching", e);
    }

    let rangeListParameter;
    if (startDate && endDate) {
      rangeListParameter = {
        startDate: moment(startDate).format("YYYY-MM-DD"),
        endDate: moment(endDate).format("YYYY-MM-DD"),
      };
    }

    try {
      const { data } = await axios.post(
        `/api/admin/rangeList?${querystring.stringify(rangeListParameter)}`
      );
      navigation({
        pathname: location.pathname,
        search: `?${querystring.stringify(rangeListParameter)}`,
      });
      setAllMentoList(data.data);
    } catch (e) {
      console.log("Error while fetching", e);
    }
  };

  React.useEffect(() => {
    fetchData && fetchData({ page: currentPage });
  }, [fetchData, currentPage]);

  function makePage() {
    const elements = [];

    if (currentPage > maxPage) {
      const button = (
        <>
          <Button
            key={startPage}
            onClick={() => {
              fetchData({ page: 1 });
              setCurrentPage(1);
            }}
            className="goFirst"
          >
            {"<<"}
          </Button>
          <Button
            key={startPage - 1}
            onClick={() => {
              fetchData({ page: 1 });
              setCurrentPage(currentPage - 1);
            }}
            className="prev"
          >
            {"< Prev"}
          </Button>
        </>
      );
      elements.push(button);
    }

    for (let page = startPage; page <= endPage; page++) {
      let buttonStyle = {};
      if (currentPage === page) {
        buttonStyle.fontWeight = "bold";
        buttonStyle.color = "#fff";
        buttonStyle.backgroundColor = "#1F3A93";
      }
      const button = (
        <Button
          style={buttonStyle}
          key={page}
          onClick={() => {
            setCurrentPage(page);
          }}
        >
          {page}
        </Button>
      );
      elements.push(button);
    }

    if (endPage < totalPage) {
      const button = (
        <>
          <Button
            key={endPage + 1}
            onClick={() => {
              // fetchData({page: endPage + 1});
              setCurrentPage(currentPage + 1);
            }}
            className="next"
          >
            {"Next >"}
          </Button>
          <Button
            key={totalPage}
            onClick={() => {
              // fetchData({page: endPage + 1});
              setCurrentPage(totalPage);
            }}
            className="goLast"
          >
            {">>"}
          </Button>
        </>
      );

      elements.push(button);
    }

    return elements;
  }

  useEffect(() => {
    let unmounted = false;
    if (!unmounted) {
      const tempList = data.filter((props, index) => {
        return selectedRowIds[index] === true;
      });
      setSelectedMentoList(tempList);
    }
    return () => {
      unmounted = true;
    };
  }, [selectedRowIds, setSelectedMentoList, data]);

  return (
    <>
      {/*{JSON.stringify(*/}
      {/*    {*/}
      {/*        dataLength: data.length,*/}
      {/*        rowLength: rows.length,*/}
      {/*        startPage,*/}
      {/*        endPage,*/}
      {/*        totalPage,*/}
      {/*    },*/}
      {/*    null,*/}
      {/*    2*/}
      {/*)}*/}
      <SearchWrap>
        <Search
          searchTerm={query.searchTerm}
          onSubmit={(value) => {
            setSearchTerm(value);
            setCurrentPage(1);
          }}
        />
      </SearchWrap>

      <TableWrap>
        <table {...getTableProps()}>
          <thead>
            {headerGroups.map((headerGroup) => (
              <tr {...headerGroup.getHeaderGroupProps()}>
                {headerGroup.headers.map((column) => (
                  <th {...column.getHeaderProps()}>
                    {column.render("Header")}
                  </th>
                ))}
              </tr>
            ))}
          </thead>
          <tbody {...getTableBodyProps()}>
            {rows.map((row, index) => {
              prepareRow(row);
              return (
                <CustomRow
                  checked={selectedRowIds[`${index}`]}
                  {...row.getRowProps()}
                >
                  {row.cells.map((cell) => (
                    <td {...cell.getCellProps()}>{cell.render("Cell")}</td>
                  ))}
                </CustomRow>
              );
            })}
          </tbody>
        </table>
      </TableWrap>
      <PageNationWrap>{makePage()}</PageNationWrap>
    </>
  );
};

const InputRadio = styled.input`
  border: 2px solid white;
  box-shadow: 0 0 0 1px #e5e5e5;
  appearance: none;
  border-radius: 50%;
  width: 14px;
  height: 14px;
  background-color: #fff;
  transition: all ease-in 0.2s;
  :checked {
    background-color: #203a8f;
  }
`;

const CustomRow = styled.tr`
  ${(props) => props.checked && `background-color : #c8cfe4;`}
`;

