import React, { useState, useContext, useEffect } from "react";
import styled from "styled-components";
import axios from "axios";
import HeadingAdmin from "../../Components/HeadingAdmin";
import { useNavigate } from "react-router-dom";
import { AuthContext } from "../../context/AuthContext";
import Footer from "../../Components/Footer";
import swal from "sweetalert";

const Area = () => {
  const [me] = useContext(AuthContext);
  const [name, setName] = useState("");
  let navigate = useNavigate();

  useEffect(() => {
    if (!window.localStorage.sessionId) {
      navigate("/admin/login");
      // setTimeout(() => {
      //   navigate("/admin/login");
      // }, 500);
    }
  }, []);

  const submitHandler = (e) => {
    if (name === "") {
      swal({
        text: " ",
        className: "hollyn",
        button: {
          text: "",
        },
      });
      return;
    } else {
      axios
        .post("/api/admin/area/register", {
          name,
        })
        .then((response) => {
          if (response.data.success) {
            swal({
              text: "",
              icon: "success",
              className: "hollyn",
              button: {
                text: "",
              },
            });
            navigate(-1);
          }
        });
    }
  };

  return (
    <>
      <HeadingAdmin />

      <MainWrap>
        <MainInner>
          <FormBox>
            <BoxWrap>
              <Title> </Title>
              <form
                onSubmit={(e) => {
                  e.preventDefault();
                  submitHandler(e);
                }}
              >
                <div style={{ textAlign: "center" }}>
                  <Input
                    value={name}
                    onChange={(e) => {
                      setName(e.target.value);
                    }}
                  />
                </div>
                <div style={{ textAlign: "center", marginTop: "100px" }}>
                  <ButtonConfirm type="submit"></ButtonConfirm>
                </div>
                <BtnGroup>
                  <ButtonBefore
                    onClick={(e) => {
                      e.preventDefault();
                      navigate("/admin/arealist");
                    }}
                  >
                    
                  </ButtonBefore>
                </BtnGroup>
              </form>
            </BoxWrap>
          </FormBox>
        </MainInner>
      </MainWrap>
      <Footer />
    </>
  );
};

const MainWrap = styled.div`
  width: 62.7604vw;
  margin: 0 auto;
  height: 230px;
  @media screen and (max-width: 1500px) {
    height: 300px;
    width: 90%;
  }
  @media (min-width: 768px) and (max-width: 1500px) and (orientation: landscape) {
    height: 310px;
  }
`;
const MainInner = styled.div`
  position: relative;
  over-flow: hidden;
`;
const FormBox = styled.div`
  position: absolute;
  width: 100%;
  top: -3.5vw;
  @media screen and (max-width: 500px) {
    top: 1%;
  }
`;
const BoxWrap = styled.div`
  background-color: white;
  box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.1);
  border-radius: 20px;
  box-size: border-box;
  padding: 5% 10.4167vw 5% 10.4167vw;
`;

const BtnGroup = styled.div`
  text-align: left;
  margin-top: 4vw;
`;
const Input = styled.input`
  font-size: 17px;
  border-radius: 6px;
  height: 40px;
  width: 20.1563vw;
  padding-left: 1%;
  background: #ffffff;
  border: 2px solid rgba(32, 58, 143, 0.3);
  box-sizing: border-box;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  @media screen and (max-width: 1500px) {
    width: 65%;
    height: 45px;
    line-height: 45px;
  }
`;
const Title = styled.h1`
  text-align: center;
  font-size: 25px;
  color: #203a8f;
  font-weight: 600;
  margin-bottom: 12%;
`;
const ButtonBefore = styled.button`
  font-size: 16px;
  font-weight: 600;
  width: 7.8125vw;
  height: 2.0833vw;
  background: #ffffff;
  border: 2px solid #858585;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  margin-right: 50%;
  &:hover {
    background-color: #858585;
    color: white;
  }
  @media screen and (max-width: 1500px) {
    width: 15vw;
    height: 4vw;
    font-size: 1.6vw;
  }
  @media (min-width: 768px) and (max-width: 1500px) and (orientation: landscape) {
    width: 10vw;
    height: 3vw;
    font-size: 1.3vw;
    margin-right: 55%;
  }
`;
const ButtonConfirm = styled.button`
  font-size: 16px;
  font-weight: 600;
  width: 7.8125vw;
  height: 2.0833vw;
  border-radius: 5px;
  cursor: pointer;
  color: #1f3a93;
  border: 2px solid #1f3a93;
  background-color: white;
  &:hover {
    color: white;
    background-color: #1f3a93;
  }
  @media screen and (max-width: 1500px) {
    width: 15vw;
    height: 4vw;
    font-size: 1.6vw;
  }
  @media (min-width: 768px) and (max-width: 1500px) and (orientation: landscape) {
    width: 10vw;
    height: 3vw;
    font-size: 1.3vw;
  }
`;
export default Area;
