const fontSizeOffset = 4.5;

const _autoFitColumns = (json) => {
  const jsonKeys = Object.keys(json[0]);
  const objectMaxLength = [];
  jsonKeys.forEach((key) => {
    objectMaxLength.push(
      pixelWidth(key, {
        size: fontSizeOffset,
      })
    );
  });

  json.forEach((data, i) => {
    const value = json[i];
    jsonKeys.forEach((key, j) => {
      const l = value[jsonKeys[j]]
        ? pixelWidth(value[jsonKeys[j]], {
            size: fontSizeOffset,
          })
        : 0;
      objectMaxLength[j] = objectMaxLength[j] >= l ? objectMaxLength[j] : l;
    });
  });
  return objectMaxLength.map((w) => {
    return { width: w };
  });
};

const detailedExportToExcel = (data) => {
  const wb = xlsx.utils.book_new();
  const ws = xlsx.utils.aoa_to_sheet(data);
  for (let i = 0; i < data.length; i++) {
    for (let j = 0; j < detailedStyleKeys.length; j++) {
      ws[`${detailedStyleKeys[j]}${i + 1}`].s = {
        alignment,
      };
    }
  }
  detailedStyleKeys.map((props) => {
    for (let i = 1; i < 3; i++) {
      ws[`${props}${i}`].s = {
        // set the style for target cell
        font: {
          sz: 11,
          bold: true,
          name: "Helvetica",
        },
        fill: {
          patternType: "solid",
          fgColor: {
            rgb: "0092D050",
          },
        },
        border: {
          top: {
            style: "thin",
            color: {
              rgb: "00A0A0A0",
            },
          },
          right: {
            style: "thin",
            color: {
              rgb: "00A0A0A0",
            },
          },
          bottom: {
            style: "thin",
            color: {
              rgb: "00A0A0A0",
            },
          },
          left: {
            style: "thin",
            color: {
              rgb: "00A0A0A0",
            },
          },
        },
        alignment,
      };
    }
  });
  const tempWidth = _autoFitColumns(data);
  ws["!cols"] = tempWidth;
  ws["!merges"] = [
    { s: { r: 0, c: 3 }, e: { r: 0, c: 6 } },
    { s: { r: 0, c: 9 }, e: { r: 0, c: 14 } },
    { s: { r: 0, c: 0 }, e: { r: 1, c: 0 } },
    { s: { r: 0, c: 1 }, e: { r: 1, c: 1 } },
    { s: { r: 0, c: 2 }, e: { r: 1, c: 2 } },
    { s: { r: 0, c: 7 }, e: { r: 1, c: 7 } },
    { s: { r: 0, c: 8 }, e: { r: 1, c: 8 } },
    { s: { r: 0, c: 15 }, e: { r: 1, c: 15 } },
    { s: { r: 0, c: 16 }, e: { r: 1, c: 16 } },
    { s: { r: 0, c: 17 }, e: { r: 1, c: 17 } },
    { s: { r: 0, c: 18 }, e: { r: 1, c: 18 } },
    { s: { r: 0, c: 19 }, e: { r: 1, c: 19 } },
  ];
  xlsx.utils.book_append_sheet(wb, ws, ""); // add worksheet to workbook
  xlsx.writeFile(wb, detailedDownloadName); // write workbook
};
