import React, { useContext, useEffect, useState } from "react";
import { AuthContext } from "../../context/AuthContext";
import axios from "axios";
import { useParams, useNavigate } from "react-router-dom";
import styled from "styled-components";
import Heading from "../../Components/Heading";
import Footer from "../../Components/Footer";
import { DatePicker, SelectPicker } from "rsuite";
import "rsuite/dist/rsuite.min.css";
import { isBefore } from "date-fns";
import emailjs from "@emailjs/browser";
import swal from "sweetalert";

const Journal = () => {
  const [number, setNumber] = useState(null); //
  const [type, setType] = useState(null); //
  const [division, setDivision] = useState(null); //
  const [content, setContent] = useState(""); //
  const [item1, setItem1] = useState(""); //()
  const [item2, setItem2] = useState(""); //()
  const [cdivision, setCdivision] = useState(""); //
  const [mento, setMento] = useState(""); //
  const [name, setName] = useState(""); //
  const [email, setEmail] = useState(""); //
  const [phoneNum, setPhoneNum] = useState(""); //
  const [ccontent, setCcontent] = useState(" "); //
  const [cetc, setCetc] = useState(" "); //
  const [etc, setEtc] = useState(" "); //
  const [answer, setAnswer] = useState(" "); //
  const [result, setResult] = useState("true"); //(, )
  const [grade, setGrade] = useState(""); //
  const [income, setIncome] = useState(""); // 
  const [me] = useContext(AuthContext);
  const [date, setDate] = useState(null);
  const [startTime, setStartTime] = useState(null);
  const [endTime, setEndTime] = useState(null);
  const [incomeProgram, setIncomeProgram] = useState(" "); // ,..
  const [incomeProgramEtc, setIncomeProgramEtc] = useState(null); // 
  const [active, setActive] = useState(); // 
  const [status, setStatus] = useState(); //emailj
  const numberData = [
    { label: "1", value: "1" },
    { label: "2", value: "2" },
    { label: "3", value: "3" },
  ];
  const typeData = [
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
  ];
  const divisionData = [
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
  ];
  const cdivisionData = [
    { label: "()-", value: "()-" },
    { label: "()-", value: "()-" },
    { label: "-", value: "-" },
    { label: "-", value: "-" },
    { label: "", value: "" },
    { label: "(/ )", value: "(/ )" },
    { label: " ", value: "" },
    { label: "/", value: "/" },
  ];
  const incomeProgramData = [
    { label: " ", value: " " },
    { label: " ", value: " " },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
  ];
  let navigate = useNavigate();
  useEffect(() => {
    if (!me) {
      // setTimeout(() => {
      //   navigate("/mento/login");
      // }, 500);
      navigate("/mento/login");
    } else {
      console.log("login");
    }
  }, []);

  const checkPhone = (e) => {
    //  
    var regExp = /^01(?:0|1|[6-9])(?:\d{3}|\d{4})\d{4}$/;
    //    true 
    var check = regExp.test(phoneNum);
    if (!check) {
      //alert("");
      return false;
    } else {
      return true;
    }
  };

  const checkEmail = (e) => {
    var regExp =
      /^[0-9a-zA-Z]([-_\.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_\.]?[0-9a-zA-Z])*\.[a-zA-Z]{2,3}$/i;
    //    true 
    //console.log("   :: ", regExp.test(email2));
    var check = regExp.test(email);
    if (!check) {
      //alert("");
      return false;
    } else {
      //alert("");
      return true;
    }
  };

  const sendEmail = (countNum) => {
    emailjs.init("XAgrL47VgEpM8LfuT");
    let templateParams = {
      name: name,
      email: email,
      link: `http://175.211.133.210/mento/survey/${countNum}`,
      mento: me.name,
    };
    console.log(templateParams);
    emailjs.send("yonsei_online", "template_95cnopg", templateParams).then(
      function (response) {
        console.log("Success!", response.status, response.text);
        setStatus("success");
      },
      function (error) {
        console.log("Failed...", error);
        setStatus("fail");
      }
    );
  };

  const onSubmit = (e) => {
    e.preventDefault();
    if (number === "1" || number === "2" || number === "3") {
      let countNum = 0;
      axios
        .post("/api/mento/getnumber")
        .then((response) => {
          console.log(response.data.countNum.postNum);
          countNum = response.data.countNum.postNum;
          sendEmail(countNum);
        })
        .catch((err) => {
          console.log(err);
        });
    }
    if (date == null || startTime == null || endTime == null) {
      swal({
        text: "   ",
        className: "hollyn",
        button: {
          text: "",
        },
      });
      return;
    }
    if (startTime > endTime) {
      swal({
        text: "  ",
        className: "hollyn",
        button: {
          text: "",
        },
      });
      return;
    }
    let checkPhoneResult = checkPhone();
    if (!checkPhoneResult) {
      swal({
        text: "   ",
        className: "hollyn",
        button: {
          text: "",
        },
      });
      return;
    }
    let checkEmailResult = checkEmail();
    if (!checkEmailResult) {
      swal({
        text: "   ",
        className: "hollyn",
        button: {
          text: "",
        },
      });
      setEmail("");
      return;
    }
    if (number === null || number === "") {
      swal({
        text: "   ",
        className: "hollyn",
        button: {
          text: "",
        },
      });
      setNumber(null);
      return;
    }
    if (division === null || division === "") {
      swal({
        text: "   ",
        className: "hollyn",
        button: {
          text: "",
        },
      });
      setDivision(null);
      return;
    }
    if (type === null || type === "") {
      swal({
        text: "   ",
        className: "hollyn",
        button: {
          text: "",
        },
      });
      setType(null);
      return;
    }
    if (cdivision === null || cdivision === "") {
      swal({
        text: "  ",
        className: "hollyn",
        button: {
          text: "",
        },
      });
      setCdivision(null);
      return;
    }
    if (incomeProgram === null || incomeProgram === "") {
      swal({
        text: "  ",
        className: "hollyn",
        button: {
          text: "",
        },
      });
      setIncomeProgramEtc(null);
      return;
    }
    if (incomeProgram === "") {
      if (incomeProgramEtc === null || incomeProgramEtc === "") {
        swal({
          text: "  ",
          className: "hollyn",
          button: {
            text: "",
          },
        });
        return;
      }
    }
    let body = {
      number,
      type,
      division,
      content,
      item1,
      item2,
      cdivision,
      mento: me.name,
      name: name,
      email: email,
      phoneNum: phoneNum,
      ccontent: ccontent,
      cetc,
      etc,
      answer,
      result,
      grade,
      income,
      date,
      startTime,
      endTime,
      active,
      incomeProgram,
      incomeProgramEtc,
    };

    axios
      .post("/api/mento/submit", body)
      .then((response) => {
        if (response.data.success) {
          swal({
            text: " ",
            className: "hollyn",
            icon: "success",
            button: {
              text: "",
            },
          });
          navigate("/mento/main");
        } else {
          swal({
            text: " ",
            className: "hollyn",
            icon: "warning",
            button: {
              text: "",
            },
          });
        }
      })
      .catch((err) => {
        console.log(err);
      });
  };
  const ranges = [
    {
      label: "Now",
      value: new Date(),
    },
  ];

  return (
    <>
      <Heading />
      <MainWrap>
        <MainInner>
          <FormBox>
            <form>
              <BoxWrap>
                <Title> </Title>
                <SelectWrap>
                  <SelectDiv>
                    <LabelSelect></LabelSelect>
                    <SelectPicker
                      data={numberData}
                      value={number}
                      placeholder=" "
                      onChange={setNumber}
                      searchable={false}
                    />
                  </SelectDiv>
                  <SelectDiv>
                    <LabelSelect></LabelSelect>
                    <SelectPicker
                      data={divisionData}
                      value={division}
                      placeholder=" "
                      onChange={setDivision}
                      searchable={false}
                    />
                  </SelectDiv>
                  <SelectDiv>
                    <LabelSelect></LabelSelect>
                    <SelectPicker
                      data={typeData}
                      value={type}
                      placeholder=" "
                      onChange={setType}
                      searchable={false}
                    />
                  </SelectDiv>
                </SelectWrap>
                <DatePickerWrap>
                  <Label> </Label>
                  <DatePicker
                    oneTap
                    ranges={[]}
                    placeholder=""
                    format="yyyy-MM-dd"
                    disabledDate={(date) => isBefore(date, new Date())}
                    onChange={(e) => {
                      setDate(e);
                      console.log(date);
                    }}
                  />
                  <DatePicker
                    ranges={[]}
                    className="datepicker"
                    placeholder=""
                    format="HH:mm"
                    hideHours={(hour) => hour < 9 || hour > 18}
                    hideMinutes={(minute) => minute % 10 !== 0}
                    onChange={(e) => {
                      setStartTime(e);
                    }}
                    value={startTime}
                  />
                  <DatePickerDash></DatePickerDash>
                  <DatePicker
                    ranges={[]}
                    className="datepicker"
                    placeholder=""
                    format="HH:mm"
                    hideHours={(hour) => hour < 9 || hour > 18}
                    hideMinutes={(minute) => minute % 10 !== 0}
                    onChange={(e) => {
                      setEndTime(e);
                    }}
                    value={endTime}
                  />
                </DatePickerWrap>
              </BoxWrap>
              <BoxWrap style={{ marginTop: "2%" }}>
                <Title style={{ fontSize: "18px" }}> </Title>
                <SelectDiv>
                  <LabelSelect></LabelSelect>
                  <SelectPicker
                    data={cdivisionData}
                    value={cdivision}
                    placeholder=" "
                    onChange={setCdivision}
                    searchable={false}
                  />
                </SelectDiv>
                <InputWrap>
                  <InputInnerWrap>
                    <Label></Label>
                    <Input
                      value={name || ""}
                      onChange={(e) => {
                        setName(e.target.value);
                      }}
                    />
                  </InputInnerWrap>
                  <InputInnerWrap>
                    <Label htmlFor="email"></Label>
                    <Input
                      id="email"
                      value={email || ""}
                      onChange={(e) => {
                        setEmail(e.target.value);
                      }}
                    />
                  </InputInnerWrap>
                </InputWrap>
                <InputWrap>
                  <InputInnerWrap>
                    <Label htmlFor="phoneNum"></Label>
                    <Input
                      id="phoneNum"
                      value={phoneNum || ""}
                      onChange={(e) => {
                        setPhoneNum(e.target.value);
                      }}
                    />
                    <InputText>'-'  </InputText>
                  </InputInnerWrap>
                  <InputInnerWrap>
                    <Label></Label>
                    <Input
                      value={ccontent || ""}
                      onChange={(e) => {
                        setCcontent(e.target.value);
                      }}
                    />
                  </InputInnerWrap>
                </InputWrap>
                <div>
                  <Label></Label>
                  <Input
                    style={{ width: "100%" }}
                    value={cetc || ""}
                    onChange={(e) => {
                      setCetc(e.target.value);
                    }}
                  />
                </div>
              </BoxWrap>
              <BoxWrap style={{ marginTop: "2%" }}>
                <Title style={{ fontSize: "18px" }}> </Title>
                <div>
                  <Label></Label>
                  <Input
                    style={{ width: "100%" }}
                    value={etc || ""}
                    onChange={(e) => {
                      setEtc(e.target.value);
                    }}
                  />
                </div>
                <div>
                  <Label></Label>
                  <TextArea
                    value={answer || ""}
                    onChange={(e) => {
                      setAnswer(e.target.value);
                    }}
                  />
                </div>
                <div>
                  <Label></Label>
                  <Input
                    style={{ width: "100%" }}
                    value={content || ""}
                    onChange={(e) => {
                      setContent(e.target.value);
                    }}
                  />
                </div>
                <SelectWrap style={{ alignItems: "end", marginBottom: "10px" }}>
                  <SelectDiv style={{ width: "29%" }}>
                    <LabelSelect></LabelSelect>
                    <SelectPicker
                      data={incomeProgramData}
                      value={incomeProgram}
                      placeholder=" "
                      onChange={setIncomeProgram}
                      searchable={false}
                    />
                  </SelectDiv>
                  <div
                    className={incomeProgram == "" ? "show" : "hide"}
                    style={{ width: "61%" }}
                  >
                    <InputProgramEtc
                      name="incomeProgramEtc"
                      onChange={(e) => {
                        setIncomeProgramEtc(e.target.value);
                      }}
                    />
                  </div>
                </SelectWrap>
                <InputText>*     </InputText>
                <AgreeWrap>
                  <InputRadio
                    type="radio"
                    id="disagree"
                    name="disagree"
                    value={"false"}
                    checked={result === "false"}
                    onChange={(e) => {
                      setResult(e.target.value);
                    }}
                  />
                  <AgreeText
                    htmlFor="disagree"
                    style={{
                      marginRight: "5%",
                    }}
                  >
                     
                  </AgreeText>
                  <InputRadio
                    type="radio"
                    id="agree"
                    name="agree"
                    value={"true"}
                    checked={result === "true"}
                    onChange={(e) => {
                      setResult(e.target.value);
                    }}
                  />
                  <AgreeText htmlFor="agree"> </AgreeText>
                </AgreeWrap>

                <BottnWrap>
                  <div style={{ textAlign: "left" }}>
                    <ButtonBefore
                      onClick={(e) => {
                        e.preventDefault();
                        navigate("/mento/main");
                      }}
                    >
                      
                    </ButtonBefore>
                  </div>
                  <ButtonConfirm
                    onClick={(e) => {
                      onSubmit(e);
                    }}
                    variant="primary"
                    type="submit"
                  >
                     
                  </ButtonConfirm>
                </BottnWrap>
              </BoxWrap>
            </form>
          </FormBox>
        </MainInner>
      </MainWrap>
      <Footer />
    </>
  );
};

