import React, { useContext, useEffect, useState, useMemo } from "react";
import { AuthContext } from "../../context/AuthContext";
import { useNavigate, Link, useLocation } from "react-router-dom";
import styled from "styled-components";
import Heading from "../../Components/Heading";
import Footer from "../../Components/Footer";
import TableJournal from "../../Components/Table/TableJournal";
import queryString from "querystring";
import { SelectPicker } from "rsuite";
import "rsuite/dist/rsuite.min.css";

const JournalList = () => {
  const location = useLocation();
  const query = queryString.parse(location.search.replace("?", ""));
  const [searchTerm, setSearchTerm] = useState(query.searchTerm || "");
  const [me] = useContext(AuthContext);
  let navigate = useNavigate();

  useEffect(() => {
    if (!window.localStorage.sessionId) {
      // setTimeout(() => {
      //   navigate("/mento/login");
      // }, 500);
      navigate("/mento/login");
    }
  }, []);

  const numberData = [
    { label: "1", value: "1" },
    { label: "2", value: "2" },
    { label: "3", value: "3" },
  ];

  const Confirm = ({ values }) => {
    return (
      <Link
        style={{
          color: "#203A8F",
        }}
        to={`/mento/journaldetail/${values}`}
      >
        <Button3></Button3>
      </Link>
    );
  };

  const Result = ({ values }) => {
    let result = `${values}`;
    return (
      <>
        {result == "true" ? (
          <Button4></Button4>
        ) : (
          <Button4></Button4>
        )}
      </>
    );
  };

  // const Check = ({ values }) => {
  //   return (
  //     <>
  //       <CheckBox type="checkbox" name="chk_info" value="check" />
  //     </>
  //   );
  // };

  const columns = useMemo(
    () => [
      {
        Header: "",
        accessor: "name",
      },
      {
        Header: "",
        accessor: "number",
      },
      {
        Header: "",
        accessor: "email",
      },
      {
        Header: "",
        accessor: "ccontent",
      },
      {
        Header: "",
        accessor: "result",
        Cell: ({ cell: { value } }) => <Result values={value} />,
      },
      {
        Header: "",
        accessor: "postNum",
        Cell: ({ cell: { value } }) => <Confirm values={value} />,
      },
    ],
    []
  );
  return (
    <>
      <Heading />
      <MainWrap>
        <MainInner>
          <ContentWrap>
            <BoxWrap>
              <SelectWrap>
                <SelectDiv>
                  <SelectPicker
                    data={numberData}
                    onChange={setSearchTerm}
                    placeholder=" "
                    searchable={false}
                  />
                </SelectDiv>
              </SelectWrap>
              <div
                style={{
                  overflow: "hidden",
                  minHeight: "820px",
                }}
              >
                <TableJournal
                  columns={columns}
                  searchTerm={searchTerm}
                  setSearchTerm={setSearchTerm}
                />
              </div>
              <BtnGroup>
                <ButtonBefore
                  onClick={(e) => {
                    e.preventDefault();
                    navigate("/mento/main");
                  }}
                >
                  
                </ButtonBefore>
              </BtnGroup>
            </BoxWrap>
          </ContentWrap>
        </MainInner>
      </MainWrap>
      <Footer />
    </>
  );
};

const MainWrap = styled.div`
  width: 62.7604vw;
  margin: 0 auto;
  height: 790px;
  @media screen and (max-width: 1500px) {
    width: 90%;
    height: 805px;
  }
  @media (min-width: 768px) and (max-width: 1200px) and (orientation: landscape) {
    height: 775px;
  }
`;
const MainInner = styled.div`
  position: relative;
  over-flow: hidden;
`;
const ContentWrap = styled.div`
  position: absolute;
  width: 100%;
  top: -3.5vw;
`;
const BoxWrap = styled.div`
  background-color: white;
  box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.1);
  border-radius: 20px;
  box-size: border-box;
  padding: 3.5417vw 9.0625vw 4.6875vw 9.0625vw;
`;
const BtnGroup = styled.div`
  @media screen and (max-width: 1500px) {
  }
`;
const Button3 = styled.button`
  width: 5vw;
  height: 35px;
  background: #ffffff;
  border: 2px solid #c7d0ee;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  font-weight: 400;
  &:hover {
    background: #203a8f;
    color: #fff;
    font-weight: 600;
  }
  @media screen and (max-width: 1500px) {
    height: 45px;
  }
`;
const Button4 = styled.button`
  width: 5vw;
  height: 35px;
  color: #1f3a93;
  background-color: transparent;
  font-weight: 600;
  &:hover {
    background: #c8cfe4;
  }
  @media screen and (max-width: 1500px) {
    height: 45px;
  }
`;
const ButtonBefore = styled.button`
  font-size: 16px;
  font-weight: 600;
  width: 7.8125vw;
  height: 2.0833vw;
  background: #ffffff;
  border: 2px solid #858585;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  position: absolute;
  bottom: 8vw;

  &:hover {
    background-color: #858585;
    color: white;
  }
  @media screen and (max-width: 1500px) {
    width: 15vw;
    height: 4vw;
    font-size: 1.6vw;
  }
  @media (min-width: 768px) and (max-width: 1500px) and (orientation: landscape) {
    width: 10vw;
    height: 3vw;
    font-size: 1.3vw;
  }
`;
const SelectWrap = styled.div`
  display: flex;
  width: 100%;
  margin-left: 0.6vw;
  @media screen and (max-width: 1023px) {
  }
  @media screen and (max-width: 767px) {
    flex-direction: column;
  }
`;
const SelectDiv = styled.div`
  margin-right: 30px;
  margin-left: 0.6vw;
  div {
    width: 12vw;
    height: 40px;
    text-align: center;
    div {
      border: 2px solid #c7d0ee !important;
    }
    input {
    }
    span {
      font-size: 14px;
      font-weight: 600;
      color: #555 !important;
      display: inline-block;
      margin-top: 0.1vw;
    }
  }

  @media (min-width: 768px) and (max-width: 1200px) and (orientation: landscape) {
    div {
      width: 18vw;
      span {
      }
    }
  }

  @media screen and (max-width: 767px) {
    div {
      width: 100%;
      height: 10vw;
    }
  }
`;
export default JournalList;
