import React, { useState, useMemo, useEffect, useContext } from "react";
import { AuthContext } from "../../context/AuthContext";
import Heading from "../../Components/Heading";
import { Link, useLocation, useNavigate } from "react-router-dom";
import styled from "styled-components";
import "moment/locale/ko";
import Footer from "../../Components/Footer";
import Table from "../../Components/Table/Table";
import queryString from "querystring";

const MainPage = () => {
  const location = useLocation();
  const query = queryString.parse(location.search.replace("?", ""));
  const [searchTerm, setSearchTerm] = useState(query.searchTerm || "");
  let navigate = useNavigate();
  const [me] = useContext(AuthContext);
  let switchMe = false;

  const Confirm = ({ values }) => {
    return (
      <Link
        style={{
          color: "#203A8F",
        }}
        to={`/mento/detail/${values}`}
      >
        <Button3></Button3>
      </Link>
    );
  };

  const Active = ({ values }) => {
    let result = `${values}`;
    return <>{result === "true" ? <Button4></Button4> : ""}</>;
  };

  const columns = useMemo(
    () => [
      {
        Header: " ",
        accessor: "name",
      },
      {
        Header: "",
        accessor: "phoneNum",
      },
      {
        Header: "",
        accessor: "email",
      },
      {
        Header: "",
        accessor: "content",
      },
      {
        Header: "",
        accessor: "active",
        Cell: ({ cell: { value } }) => <Active values={value} />,
      },
      {
        Header: "",
        accessor: "postNum",
        Cell: ({ cell: { value } }) => <Confirm values={value} />,
      },
    ],
    []
  );
  useEffect(() => {
    if (!window.localStorage.sessionId) {
      // setTimeout(() => {
      //   navigate("/mento/login");
      // }, 500);
      navigate("/mento/login");
    }
  }, []);
  return (
    <>
      <Heading />
      <MainWrap>
        <MainInner>
          <ContentWrap>
            <BoxWrap>
              <BtnGroup>
                <Link to="/mento/journal">
                  <Button> </Button>
                </Link>
                <Button
                  style={{
                    background: "rgba(217, 217, 217, 0.29)",
                    color: "#BEBEBE",
                    marginLeft: "1.1458vw",
                  }}
                >
                   
                </Button>
              </BtnGroup>
              <Table
                columns={columns}
                setSearchTerm={setSearchTerm}
                searchTerm={searchTerm}
              />
            </BoxWrap>
          </ContentWrap>
        </MainInner>
      </MainWrap>
      <Footer />
    </>
  );
};

const MainWrap = styled.div`
  width: 62.7604vw;
  margin: 0 auto;
  height: 845px;
  @media screen and (max-width: 1500px) {
    width: 90%;
    height: 820px;
  }
  @media (min-width: 768px) and (max-width: 1200px) and (orientation: landscape) {
    height: 810px;
  }
`;
const MainInner = styled.div`
  position: relative;
`;
const ContentWrap = styled.div`
  position: absolute;
  width: 100%;
  top: -3.5vw;
`;
const BoxWrap = styled.div`
  background-color: white;
  box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.1);
  border-radius: 20px;
  padding: 3.5417vw 9.0625vw 4.6875vw 9.0625vw;
`;
const BtnGroup = styled.div`
  text-align: center;
  margin-bottom: 3.75vw;
  @media screen and (max-width: 1500px) {
  }
`;
const Button = styled.button`
  color: white;
  background-color: #203a8f;
  border-radius: 5px;
  width: 10.4167vw;
  height: 2.0833vw;
  font-size: 16px;
  font-weight: 600;
  @media screen and (max-width: 1500px) {
    width: 13vw;
    height: 5vw;
    font-size: 0.9rem;
  }
  @media (min-width: 768px) and (max-width: 1500px) and (orientation: landscape) {
    width: 13vw;
    height: 4vw;
    font-size: 0.9rem;
  }
`;
const Button3 = styled.button`
  width: 5vw;
  height: 35px;
  background: #ffffff;
  border: 2px solid #c7d0ee;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  font-weight: 400;
  &:hover {
    background: #203a8f;
    color: #fff;
    font-weight: 600;
    border: 1px solid #203a8f;
  }
  @media screen and (max-width: 1500px) {
    width: 9vw;
  }
  @media (min-width: 768px) and (max-width: 1500px) and (orientation: landscape) {
    width: 8vw;
  }
`;
const Button4 = styled.button`
  width: 5vw;
  height: 35px;
  color: #1f3a93;
  background-color: transparent;
  font-weight: 600;
  &:hover {
    background: #c8cfe4;
  }
  @media screen and (max-width: 1500px) {
    height: 45px;
  }
`;
export default MainPage;
