import React, { useContext, useState, useEffect } from "react";
import HeadingAdmin from "../../Components/HeadingAdmin";
import { AuthContext } from "../../context/AuthContext";
import { useNavigate, Link } from "react-router-dom";
import styled from "styled-components";
import Footer from "../../Components/Footer";
import axios from "axios";

const AreaList = () => {
  const [me] = useContext(AuthContext);
  let navigate = useNavigate();
  const [mentoList, setmentoList] = useState([]);
  useEffect(() => {
    if (!window.localStorage.sessionId) {
      navigate("/admin/login");
      // setTimeout(() => {
      //   navigate("/admin/login");
      // }, 500);
    }
    axios
      .post("/api/admin/area/list")
      .then((response) => {
        if (response.data.success) {
          setmentoList([...response.data.areaList]);
        }
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);
  console.log(mentoList);
  return (
    <>
      <HeadingAdmin />

      <MainWrap>
        <MainInner>
          <FormBox>
            <BoxWrap>
              <Title> </Title>
              <ButtonWrap>
                {mentoList.map((content, idx) => {
                  return (
                    <Link to={`/admin/arealist/${content.postNum}`}>
                      <Button>{content.name}</Button>
                    </Link>
                  );
                })}
              </ButtonWrap>
              <div
                style={{
                  textAlign: "center",
                }}
              >
                <Link
                  style={{
                    textDecoration: "none",
                    color: "#203a8f",
                  }}
                  to="/admin/area"
                >
                  <ButtonConfirm> </ButtonConfirm>
                </Link>
              </div>
              <div style={{ marginTop: "3vw" }}>
                <ButtonBefore
                  onClick={(e) => {
                    e.preventDefault();
                    navigate("/admin/main");
                  }}
                >
                  
                </ButtonBefore>
              </div>
            </BoxWrap>
          </FormBox>
        </MainInner>
      </MainWrap>
      <Footer />
    </>
  );
};

const MainWrap = styled.div`
  width: 62.7604vw;
  margin: 0 auto;
  height: 380px;
  @media screen and (max-width: 1500px) {
    width: 90%;
    height: 400px;
  }
  @media (min-width: 768px) and (max-width: 1500px) and (orientation: landscape) {
    height: 440px;
  }
`;
const MainInner = styled.div`
  position: relative;
  over-flow: hidden;
`;
const FormBox = styled.div`
  position: absolute;
  width: 100%;
  top: -3.5vw;
  @media screen and (max-width: 500px) {
    top: 1%;
  }
`;
const BoxWrap = styled.div`
  background-color: white;
  box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.1);
  border-radius: 20px;
  box-size: border-box;
  padding: 7% 10.4167vw 7% 10.4167vw;
  @media screen and (max-width: 1500px) {
    padding: 7% 2% 7% 2%;
  }
`;

const Button = styled.div`
  font-size: 14px;
  color: #555;
  display: inline-block;
  width: 12vw;
  text-align: center;
  height: 40px;
  line-height: 40px;
  margin-right: 2%;
  margin-bottom: 12px;
  background: #ffffff;
  border: 2px solid #c7d0ee;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  &:hover {
    background: #203a8f;
    color: white;
    border: 1px solid #203a8f;
  }
`;

const Title = styled.h1`
  text-align: center;
  font-size: 25px;
  color: #203a8f;
  font-weight: 600;
  margin-bottom: 12%;
`;
const ButtonWrap = styled.div`
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 60px;
  justify-content: space-between;
  @media screen and (max-width: 1500px) {
    width: 70%;
    margin-left: 15%;
  }
  @media (min-width: 768px) and (max-width: 1500px) and (orientation: landscape) {
  }
`;
const ButtonBefore = styled.button`
  font-size: 16px;
  font-weight: 600;
  width: 7.8125vw;
  height: 2.0833vw;
  background: #fff;
  border: 2px solid #858585;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  margin-right: 50%;
  &:hover {
    background-color: #858585;
    color: white;
  }
  @media screen and (max-width: 1500px) {
    width: 15vw;
    height: 4vw;
    font-size: 1.6vw;
  }
  @media (min-width: 768px) and (max-width: 1500px) and (orientation: landscape) {
    width: 10vw;
    height: 3vw;
    font-size: 1.3vw;
    margin-left: 15%;
  }
`;
const ButtonConfirm = styled.button`
  font-size: 16px;
  font-weight: 600;
  width: 7.8125vw;
  height: 2.0833vw;
  border-radius: 5px;
  cursor: pointer;
  color: #203a8f;
  border: 2px solid #1f3a93;
  background-color: white;
  &:hover {
    color: white;
    background-color: #1f3a93;
  }
  @media screen and (max-width: 1500px) {
    width: 15vw;
    height: 4vw;
    font-size: 1.6vw;
  }
  @media (min-width: 768px) and (max-width: 1500px) and (orientation: landscape) {
    width: 10vw;
    height: 3vw;
    font-size: 1.3vw;
  }
`;
export default AreaList;
