export const dateToStr = (format, type) => {
  let year = format.getFullYear();
  let month = String(format.getMonth() + 1);
  if (Number(month) < 10) month = "0" + month;
  let date = String(format.getDate());
  if (Number(date) < 10) date = "0" + date;
  let hour = String(format.getHours());
  if (Number(hour) < 10) hour = "0" + hour;
  let min = String(format.getMinutes());
  if (Number(min) < 10) min = "0" + min;
  let sec = String(format.getSeconds());
  if (Number(sec) < 10) sec = "0" + sec;
  let milliseconds = String(format.getMilliseconds());
  if (Number(milliseconds) < 10) milliseconds = "0" + milliseconds;

  if (type === "day") {
    return `${year}-${month}-${date}`;
  } else if (type === "time") {
    return { hour: Number(hour), min: Number(min) };
  }
};

export default dateToStr;
