import React, { useContext } from "react";
import { Link } from "react-router-dom";
import { AuthContext } from "../context/AuthContext";
import axios from "axios";
import styled from "styled-components";

const Heading = () => {
  const [me, setMe] = useContext(AuthContext);
  const logoutHandler = async () => {
    await axios
      .patch("/api/users/logout")
      .then((response) => {
        setMe();
      })
      .catch((err) => {
        console.log(err);
      });
  };

  return (
    <MainWrap style={{ backgroundColor: "#1F3A93" }}>
      <InnerWrap>
        <BtnWrap>
          {/* <Link to="/mento" style={{ marginRight: "5%" }}>
        Home
      </Link> */}
          {/* <Link to="/mento/register">Register</Link> */}
          {me ? (
            <ButtonWrap>
              <Link style={{ color: "white" }} to="/mento/list">
                <Button style={{ marginRight: "2%" }}></Button>
              </Link>
              <Link
                to="/mento/login"
                onClick={() => {
                  logoutHandler();
                }}
              >
                <Button></Button>
              </Link>
            </ButtonWrap>
          ) : (
            <>
              <Link to="/mento/login">Login</Link>
            </>
          )}
        </BtnWrap>
        <Logo className="image_large">
          <Link to="/mento/main">
            <img
              style={{ width: "5.2083vw" }}
              src="/image/login/logo.png"
              alt=""
            />
          </Link>
        </Logo>

        <MainInner>
          <div>
            <Img
              src="/image/common/topWhole.png"
              alt="begin start up sailing"
            />
          </div>
        </MainInner>
      </InnerWrap>
    </MainWrap>
  );
};

const MainWrap = styled.div`
  max-width: 100%;
  height: auto
  background-color: #1f3a93;
  position: relative;
 
`;
const InnerWrap = styled.div`
  width: 62.7604vw;
  margin: 0 auto;
  @media screen and (max-width: 1500px) {
    width: 90%;
  }
`;
const MainInner = styled.div``;
const Button = styled.button`
  background-color: #96a2c9;
  width: 8.0729vw;
  height: 2.0833vw;
  border-radius: 5px;
  color: #fff;
  font-size: 0.7292vw;
  font-weight: 500;
  &:hover {
    background-color: #bebebe;
    color: #fff;
    font-weight: 800;
  }
  @media screen and (max-width: 1500px) {
    width: 15vw;
    height: 4vw;
    font-size: 1.6vw;
  }
  @media (min-width: 768px) and (max-width: 1500px) and (orientation: landscape) {
    width: 13vw;
    height: 3vw;
    font-size: 1.3vw;
  }
`;
const BtnWrap = styled.div`
  position: absolute;
  right: 19vw;
  top: 2vw;
  width: 600px;
  @media (min-width: 768px) and (max-width: 1500px) and (orientation: landscape) {
    right: 10vw;
  }
  @media screen and (max-width: 1500px) {
    right: 6vw;
  }
`;
const Img = styled.img`
  width: 100%;
`;
const ButtonWrap = styled.div`
  box-sizing: border-box;
  text-align: right;
`;
const Logo = styled.div`
  position: absolute;
  top: 2vw;
  left: 13vw;
  @media screen and (max-width: 1500px) {
    left: 4.5vw;
  }
`;
export default Heading;
