const baseColor = "#203A8F";
const baseColorRGBStroke = "rgba(32,58,143,0.3)";
const fromConsultation = [" ", "  ", " "];
const pointList = ["5", "4", "3", "2", "1"];
const questionB = [
  "   .",
  "    .",
  "    .",
  "     .",
];
const questionC = [
  "    .",
  "       .",
  "      .",
  "     .",
  "  ()   .",
];

const CommonComponent = ({ questions, answers, setAnswers }) => {
  return (
    <>
      {questions.map((props, index) => (
        <div key={index}>
          <QuestionText2Wrap>
            <Row>
              <Circle>
                <span>{index + 1}</span>
              </Circle>
              <QuestionText2 style={{ fontSize: "17px", color: "#555555" }}>
                {props}
              </QuestionText2>
            </Row>
          </QuestionText2Wrap>
          <Row2
            style={{
              marginBottom: questions.length - 1 !== index ? "38px" : "0px",
            }}
          >
            {pointList.map((props2, index2) => (
              <Row key={index2} style={{ height: "35px" }}>
                <InputRadio
                  style={{ color: baseColor, marginRight: "11px" }}
                  value={answers[`${index}`]}
                  checked={answers[`${index}`] === index2}
                  onChange={() => {
                    setAnswers((prev) => ({ ...prev, [`${index}`]: index2 }));
                  }}
                  type="checkbox"
                />
                <QuestionText2>{props2}</QuestionText2>
              </Row>
            ))}
          </Row2>
        </div>
      ))}
    </>
  );
};

const Survey = () => {
  const [selectButton, setSelectedButton] = useState("");
  const [answerA, setAnswerA] = useState(0);
  const [answerB, setAnswerB] = useState({ 0: 0, 1: 0, 2: 0, 3: 0 });
  const [answerC, setAnswerC] = useState({ 0: 0, 1: 0, 2: 0, 3: 0, 4: 0 });
  const [textArea, setTextArea] = useState("");
  const [tab, setTab] = useState("1");
  let params = useParams();
  let navigate = useNavigate();

  const sum = () => {
    let tempA = 0;
    let tempB = 0;
    let sum = 0;
    for (let i = 0; i < 4; i++) {
      switch (answerB[i]) {
        case 0:
          console.log(5);
          tempA = tempA + 5;
          break;
        case 1:
          console.log(4);
          tempA = tempA + 4;
          break;
        case 2:
          console.log(3);
          tempA = tempA + 3;
          break;
        case 3:
          console.log(2);
          tempA = tempA + 2;
          break;
        case 4:
          console.log(1);
          tempA = tempA + 1;
          break;
      }
    }
    for (let i = 0; i < 5; i++) {
      switch (answerC[i]) {
        case 0:
          console.log(5);
          tempB = tempB + 5;
          break;
        case 1:
          console.log(4);
          tempB = tempB + 4;
          break;
        case 2:
          console.log(3);
          tempB = tempB + 3;
          break;
        case 3:
          console.log(2);
          tempB = tempB + 2;
          break;
        case 4:
          console.log(1);
          tempB = tempB + 1;
          break;
      }
    }
    sum = ((tempA + tempB) / 9).toFixed(2);
    return parseFloat(sum);
  };

  const submit = () => {
    let sumReslut = sum();
    let body = {
      postNum: params.postNum,
      grade: sumReslut,
      receipt: selectButton,
      income: answerA,
    };
    axios
      .post("/api/mento/survey/submit", body)
      .then((response) => {
        if (response.data.success) {
          swal({
            text: "",
            icon: "success",
            className: "hollyn",
            button: {
              text: "",
            },
          });
        }
      })
      .catch((err) => {
        console.log(err);
      });
  };

  return (
    <>
      <HeadingCommon />
      <MainWrap>
        <MainInner>
          <Column>
            <BoxWrap style={{ height: "509px" }}>
              <Title style={{ marginBottom: "78px" }}>
                   
              </Title>
              <Row style={{ marginBottom: "43px" }}>
                <Button
                  value={""}
                  className={`btn ${tab === "0" ? "active" : ""}`}
                  onClick={() => {
                    setTab("0");
                    setSelectedButton("");
                  }}
                >
                   
                </Button>
                <Button
                  className={`btn ${tab === "1" ? "active" : ""}`}
                  value={""}
                  onClick={() => {
                    setTab("1");
                    setSelectedButton("");
                  }}
                >
                   
                </Button>
              </Row>
              <QuestionTextWrap>
                <QuestionText>  </QuestionText>
              </QuestionTextWrap>
              <Row2>
                {fromConsultation.map((props, index) => (
                  <Row key={index} style={{ height: "35px" }}>
                    <InputRadio
                      value={index}
                      checked={answerA === index}
                      onChange={() => {
                        setAnswerA(index);
                      }}
                      style={{ color: baseColor, marginRight: "11px" }}
                      type="checkbox"
                    />
                    <QuestionText2>{props}</QuestionText2>
                  </Row>
                ))}
              </Row2>
            </BoxWrap>
            <BoxWrap style={{ height: "599px" }}>
              <Title2>  </Title2>
              <CommonComponent
                questions={questionB}
                answers={answerB}
                setAnswers={setAnswerB}
              />
            </BoxWrap>
            <BoxWrap style={{ height: "723px" }}>
              <Title2></Title2>
              <CommonComponent
                questions={questionC}
                answers={answerC}
                setAnswers={setAnswerC}
              />
            </BoxWrap>
            <BoxWrap style={{ height: "371px" }}>
              <Title2> </Title2>
              <TextArea
                value={textArea}
                onChange={(e) => setTextArea(e.target.value)}
              >
                {textArea}
              </TextArea>
              <Button3
                onClick={(e) => {
                  submit(e);
                }}
              >
                
              </Button3>
            </BoxWrap>
          </Column>
        </MainInner>
      </MainWrap>
      <Footer />
    </>
  );
};

