const SVGRect = ({ selectedJournal, dataKey, value }) => {
  console.log(selectedJournal);
  return (
    <Svg width={checkBoxSize} height={checkBoxSize}>
      {selectedJournal[dataKey] === value && (
        <Polyline points="2,5 3,10 10,3" stroke="black" strokeWidth={1} />
      )}
      <Rect width={checkBoxSize} height={checkBoxSize} stroke={1} />
    </Svg>
  );
};

