import React, { useContext, useEffect, useState } from "react";
import { useParams, useNavigate } from "react-router-dom";
import axios from "axios";
import styled from "styled-components";
import HeadingAdmin from "../../Components/HeadingAdmin";
import Footer from "../../Components/Footer";
import { AuthContext } from "../../context/AuthContext";
import swal from "sweetalert";

const AreaDetail = () => {
  const [me] = useContext(AuthContext);
  const [content, setContent] = useState({});
  let params = useParams();
  let navigate = useNavigate();

  useEffect(() => {
    if (!window.localStorage.sessionId) {
      navigate("/admin/login");
      // setTimeout(() => {
      //   navigate("/admin/login");
      // }, 500);
    }
    let body = {
      postNum: params.postNum,
    };
    axios
      .post("/api/admin/area/detail", body)
      .then((response) => {
        if (response.data.success) {
          //console.log(response);
          setContent(response.data.post);
        }
      })
      .catch((err) => {
        console.log(err);
      });
    //console.log(params);
  }, []);
  const DeleteHandler = async () => {
    let body = {
      postNum: params.postNum,
    };
    let result = await swal({
      text: " ?",
      className: "hollyn",
      buttons: ["", ""],
    });
    if (result) {
      axios
        .post("/api/admin/area/delete", body)
        .then((response) => {
          if (response.data.success) {
            swal({
              text: " ",
              icon: "success",
              className: "hollyn",
              button: {
                text: "",
              },
            });
            navigate("/admin/arealist");
          }
        })
        .catch((err) => {
          swal({
            text: "  ",
            icon: "warning",
            className: "hollyn",
            button: {
              text: "",
            },
          });
        });
    }
  };

  return (
    <>
      <HeadingAdmin />
      <MainWrap>
        <MainInner>
          <FormBox>
            <BoxWrap>
              <Title></Title>
              <div style={{ textAlign: "center" }}></div>
              <Ptag>{content.name}</Ptag>
              <div style={{ textAlign: "center", marginTop: "100px" }}>
                <ButtonDelete
                  onClick={() => {
                    DeleteHandler();
                  }}
                >
                   
                </ButtonDelete>
              </div>
              <BtnGroup>
                <ButtonBefore
                  onClick={(e) => {
                    e.preventDefault();
                    navigate("/admin/arealist");
                  }}
                >
                  
                </ButtonBefore>

                {/* <Button
              onClick={(e) => {
                activeHandler(e);
              }}
            >
              
            </Button> */}
              </BtnGroup>
            </BoxWrap>
          </FormBox>
        </MainInner>
      </MainWrap>
      <Footer />
    </>
  );
};

const MainWrap = styled.div`
  width: 62.7604vw;
  margin: 0 auto;
  height: 220px;
  @media screen and (max-width: 1500px) {
    width: 90%;
  }
`;
const MainInner = styled.div`
  position: relative;
  over-flow: hidden;
`;
const FormBox = styled.div`
  position: absolute;
  width: 100%;
  top: -3.5vw;
  @media screen and (max-width: 500px) {
    top: 1%;
  }
`;
const BoxWrap = styled.div`
  background-color: white;
  box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.1);
  border-radius: 20px;
  box-size: border-box;
  padding: 5% 10.4167vw 5% 10.4167vw;
`;
const Title = styled.h1`
  text-align: center;
  font-size: 25px;
  color: #203a8f;
  font-weight: 600;
  margin-bottom: 12%;
`;
const Ptag = styled.p`
  width: 20.1563vw;
  background: #ffffff;
  border: 2px solid #c7d0ee;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  height: 40px;
  line-height: 35px;
  padding-left: 1%;
  margin: 0 auto;
  text-align: center;
  font-size: 14px;
`;
const BtnGroup = styled.div`
  text-align: left;
  margin-top: 4vw;
`;
const ButtonBefore = styled.button`
  font-size: 16px;
  font-weight: 600;
  width: 7.8125vw;
  height: 2.0833vw;
  background: #fff;
  border: 2px solid #858585;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  &:hover {
    background-color: #858585;
    color: white;
  }
  @media screen and (max-width: 1500px) {
    width: 15vw;
    height: 4vw;
    font-size: 1.6vw;
  }
  @media (min-width: 768px) and (max-width: 1500px) and (orientation: landscape) {
    width: 10vw;
    height: 3vw;
    font-size: 1.3vw;
    margin-right: 55%;
  }
`;
const ButtonDelete = styled.button`
  font-size: 16px;
  font-weight: 600;
  width: 7.8125vw;
  height: 2.0833vw;
  color: #ff4949;
  border: 2px solid #ff4949;
  background-color: white;
  border-radius: 5px;
  margin-left: 3%;
  &:hover {
    color: white;
    background-color: #ff4949;
  }
  @media screen and (max-width: 1500px) {
    width: 15vw;
    height: 4vw;
    font-size: 1.6vw;
  }
  @media (min-width: 768px) and (max-width: 1500px) and (orientation: landscape) {
    width: 10vw;
    height: 3vw;
    font-size: 1.3vw;
  }
`;

export default AreaDetail;
