import React, { useContext } from "react";
import { Link } from "react-router-dom";
import { AuthContext } from "../context/AuthContext";
import axios from "axios";
import styled from "styled-components";

const HeadingAdmin = () => {
  const [me, setMe] = useContext(AuthContext);
  console.log(me);
  const logoutHandler = async () => {
    await axios
      .patch("/api/users/admin/logout")
      .then((response) => {
        setMe();
      })
      .catch((err) => {
        console.log(err);
      });
  };

  return (
    <MainWrap style={{ backgroundColor: "#1F3A93" }}>
      <InnerWrap>
        <BtnWrap>
          {/* <Link to="/admin">Home</Link> */}
          {me ? (
            <Link
              to="/admin/login"
              onClick={() => {
                logoutHandler();
              }}
            >
              <Button></Button>
            </Link>
          ) : (
            <>
              <Link to="/admin/login">Login</Link>
            </>
          )}
        </BtnWrap>
        <Logo className="image_large">
          <Link to="/admin/main">
            <img
              style={{ width: "5.2083vw" }}
              src="/image/login/logo.png"
              alt=""
            />
          </Link>
        </Logo>

        <MainInner>
          <div>
            <Img
              src="/image/common/topWhole.png"
              alt="begin start up sailing"
            />
          </div>
        </MainInner>
      </InnerWrap>
    </MainWrap>
  );
};

const MainWrap = styled.div`
  width: 100%;
  height: auto
  background-color: #1f3a93;
  position: relative;
`;
const InnerWrap = styled.div`
  width: 62.7604vw;
  margin: 0 auto;
  @media screen and (max-width: 1500px) {
    width: 90%;
  }
`;
const MainInner = styled.div``;
const Button = styled.button`
  background-color: #96a2c9;
  width: 8.0729vw;
  height: 2.0833vw;
  border-radius: 5px;
  color: #fff;
  font-size: 0.7292vw;
  font-weight: 500;
  &:hover {
    background-color: #bebebe;
    color: #fff;
    font-weight: 800;
  }
  @media screen and (max-width: 1500px) {
    width: 15vw;
    height: 4vw;
    font-size: 1.6vw;
  }
  @media (min-width: 768px) and (max-width: 1500px) and (orientation: landscape) {
    width: 13vw;
    height: 3vw;
    font-size: 1.3vw;
  }
`;
const BtnWrap = styled.div`
  position: absolute;
  right: 19vw;
  top: 2vw;
  @media screen and (max-width: 1500px) {
    right: 6vw;
  }
  @media (min-width: 768px) and (max-width: 1500px) and (orientation: landscape) {
  }
`;
const Img = styled.img`
  width: 100%;
`;
const Logo = styled.div`
  position: absolute;
  top: 2vw;
  left: 13vw;
  @media screen and (max-width: 1500px) {
    left: 4.5vw;
  }
`;
export default HeadingAdmin;
