import React, { useState, useCallback, useContext } from "react";
import { useTable, useGlobalFilter, useFilters } from "react-table";
import { useLocation, useNavigate } from "react-router-dom";
import Search from "./Search";
import queryString from "querystring";
import axios from "axios";
import querystring from "querystring";
import styled from "styled-components";
import { AuthContext } from "../../context/AuthContext";

const TableJournal = ({ columns, searchTerm, setSearchTerm }) => {
  const [me] = useContext(AuthContext);
  const navigation = useNavigate();
  const location = useLocation();
  const query = queryString.parse(location.search.replace("?", ""));

  const [data, setData] = useState([]);
  const [currentPage, setCurrentPage] = useState(parseInt(query.page) || 1);
  const [, setCountAll] = useState(0);
  const [totalPage, setTotalPage] = useState(0);
  const [startPage, setStartPage] = useState(0);
  const [endPage, setEndPage] = useState(0);
  //    
  const [maxPage, setMaxPage] = useState(parseInt(query.maxPage) || 5);
  //    
  const [perPage, setPerPage] = useState(parseInt(query.perPage) || 9);

  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    rows,
    prepareRow,
    setGlobalFilter,
  } = useTable({ columns, data, manualPagination: true }, useGlobalFilter);

  const fetchAPIData = useCallback(
    async ({ page }) => {
      let queryParams = { page, perPage, maxPage, searchTerm };
      if (me !== undefined) {
        try {
          const { data } = await axios.post(
            `/api/mento/journallist?${querystring.stringify(queryParams)}`,
            me
          );
          navigation({
            pathname: location.pathname,
            search: `?${querystring.stringify(queryParams)}`,
          });
          const paging = data.paging;
          setCountAll(paging.countAll);
          setStartPage(paging.startPage);
          setEndPage(paging.endPage);
          setMaxPage(paging.maxPage);
          setTotalPage(paging.totalPage);
          setData(data.journallist);
          console.log(paging);
        } catch (e) {
          console.log("Error while fetching", e);
        }
      }
    },
    [location.pathname, maxPage, me, navigation, perPage, searchTerm]
  );

  const fetchData = useCallback(
    ({ page }) => {
      fetchAPIData({
        page,
      }).catch((err) => {
        console.error(err);
      });
    },
    [fetchAPIData]
  );

  React.useEffect(() => {
    fetchData && fetchData({ page: currentPage });
  }, [fetchData, currentPage]);

  function makePage() {
    const elements = [];

    if (currentPage > maxPage) {
      const button = (
        <>
          <Button
            key={startPage}
            onClick={() => {
              fetchData({ page: 1 });
              setCurrentPage(1);
            }}
            className="goFirst"
          >
            {"<<"}
          </Button>
          <Button
            key={startPage - 1}
            onClick={() => {
              fetchData({ page: 1 });
              setCurrentPage(currentPage - 1);
            }}
            className="prev"
          >
            {"< Prev"}
          </Button>
        </>
      );
      elements.push(button);
    }

    for (let page = startPage; page <= endPage; page++) {
      let buttonStyle = {};
      if (currentPage === page) {
        buttonStyle.fontWeight = "bold";
        buttonStyle.color = "#fff";
        buttonStyle.backgroundColor = "#1F3A93";
      }
      const button = (
        <Button
          style={buttonStyle}
          key={page}
          onClick={() => {
            setCurrentPage(page);
          }}
        >
          {page}
        </Button>
      );
      elements.push(button);
    }

    if (endPage < totalPage) {
      const button = (
        <>
          <Button
            key={endPage + 1}
            onClick={() => {
              // fetchData({page: endPage + 1});
              setCurrentPage(currentPage + 1);
            }}
            className="next"
          >
            {"Next >"}
          </Button>
          <Button
            key={totalPage}
            onClick={() => {
              // fetchData({page: endPage + 1});
              setCurrentPage(totalPage);
            }}
            className="goLast"
          >
            {">>"}
          </Button>
        </>
      );

      elements.push(button);
    }

    return elements;
  }

  return (
    <>
      <SearchWrap>
        <Search
          searchTerm={query.searchTerm}
          onSubmit={(value) => {
            setSearchTerm(value);
            setCurrentPage(1);
          }}
        />
      </SearchWrap>

      <TableWrap>
        <table {...getTableProps()}>
          <thead>
            {headerGroups.map((headerGroup) => (
              <tr {...headerGroup.getHeaderGroupProps()}>
                {headerGroup.headers.map((column) => (
                  <th {...column.getHeaderProps()}>
                    {column.render("Header")}
                  </th>
                ))}
              </tr>
            ))}
          </thead>
          <tbody {...getTableBodyProps()}>
            {rows.map((row) => {
              prepareRow(row);
              return (
                <tr {...row.getRowProps()}>
                  {row.cells.map((cell) => (
                    <td {...cell.getCellProps()}>{cell.render("Cell")}</td>
                  ))}
                </tr>
              );
            })}
          </tbody>
        </table>
      </TableWrap>
      <PageNationWrap>{makePage()}</PageNationWrap>
    </>
  );
};

