import React, { useContext, useState } from "react";
import HeadingAdmin from "../../Components/HeadingAdmin";
import { AuthContext } from "../../context/AuthContext";
import styled from "styled-components";
import Footer from "../../Components/Footer";

const ModifyArea = () => {
  const [me] = useContext(AuthContext);
  const [name, setName] = useState("");

  const onSubmit = (e) => {
    e.preventDefault();
  };

  return (
    <>
      <HeadingAdmin />;
      <MainWrap>
        <MainInner>
          <Title></Title>
          <div style={{ marginTop: "60px" }}>
            <Label></Label>
            <Input
              value={name}
              onChange={(e) => {
                setName(e.target.value);
              }}
            />
          </div>
          <BottnWrap>
            <Button
              onClick={(e) => {
                onSubmit(e);
              }}
              variant="primary"
              type="submit"
            >
              
            </Button>
          </BottnWrap>
        </MainInner>
      </MainWrap>
      <Footer />
    </>
  );
};

const MainWrap = styled.div`
  width: 80%;
  margin: 0 auto;
  min-height: 600px;
  position: relative;
`;
const MainInner = styled.div`
  position: absolute;
  width: 100%;
  box-shadow: 4px 4px 10px grey;
  background-color: white;
  border-radius: 15px;
  height: 100%;
  padding: 7% 10% 0% 10%;
`;
const Title = styled.h1`
  text-align: center;
  color: #203a9f;
`;
const Label = styled.label`
  text-align: left;
  display: block;
  font-size: 22px;
  font-color: #555;
  margin-top: 20px;
  margin-bottom: 10px;
`;
const Input = styled.input`
  border: 2px solid rgba(32, 54, 148, 0.3);
  border-radius: 6px;
  height: 32px;
  width: 30%;
  padding-left: 1%;
`;
const BottnWrap = styled.div`
  text-align: center;
  margin-top: 10%;
`;
const Button = styled.button`
  font-size: 18px;
  height: 48px;
  line-height: 48px;
  width: 160px;
  color: white;
  background-color: #203a9f;
  border-radius: 30px;
`;
export default ModifyArea;
