import React, { useState } from "react";
import styled from "styled-components";

function Search({ onSubmit, searchTerm }) {
  console.log("searchTerm - ", searchTerm);
  const handleSubmit = (event) => {
    event.preventDefault();
    onSubmit(event.target.elements.filter.value);
  };

  const [textValue, setTextValue] = useState(searchTerm);

  function onChange(event) {
    setTextValue(event.target.value);
  }

  return (
    <form onSubmit={handleSubmit}>
      <Div>
        <Input
          name="filter"
          value={textValue}
          onChange={onChange}
          placeholder=" "
        />
        <Button></Button>
      </Div>
    </form>
  );
}

const Div = styled.div`
  height: 40px;
  width: 100%;
  padding-left: 24px;
  margin-bottom: 34px;
  display: flex;
  @media screen and (max-width: 1500px) {
    height: 35px;
    padding-left: 0px;
  }
  @media (min-width: 768px) and (max-width: 1500px) and (orientation: landscape) {
    margin-bottom: 20px;
    padding-left: 0px;
  }
`;

const Button = styled.button`
  width: 5.2083vw;
  height: 100%;
  margin-left: 0.8333vw;
  background: #fff;
  color: #203a8f;
  font-weight: 600;
  border: 2px solid #203a8f;
  border-radius: 5px;
  font-size: 17px;
  &:hover {
    background: #203a8f;
    color: #fff;
    font-weight: 600;
  }
  @media screen and (max-width: 1500px) {
    width: 12vw;
  }
  @media (min-width: 768px) and (max-width: 1500px) and (orientation: landscape) {
    width: 8vw;

    font-size: 1.4vw;
  }
`;
const Input = styled.input`
  width: 84%;
  height: 100%;
  background: #ffffff;
  border: 2px solid #c7d0ee;
  border-radius: 5px;
  padding-left: 1.2%;
  &::placeholder {
    background-image: url(/image/common/search.png);
    background-size: contain;
    background-repeat: no-repeat;
    width: 25px;
    height: 25px;
    line-height: 22px;
  }
  @media screen and (max-width: 1500px) {
    width: 81%;
  }
  @media (min-width: 768px) and (max-width: 1500px) and (orientation: landscape) {
    width: 87%;

    font-size: 1.4vw;
    &::placeholder {
      width: 20px;

      line-height: 18px;
    }
  }
`;
export default Search;
